.beer-container {
  text-align: center;
  margin-bottom: 60px;
  padding: 40px 0 80px;
  position: relative
}
.beer-container:hover{
  box-shadow: 0 6px 10px rgba(46, 41, 51, 0.08), 0 8px 14px rgba(71, 63, 79, 0.08);
}
.beer-container h2 {
  color: #222;
  font-size: 18px
}
.beer-container p {
  position: relative;
  bottom: 5px;
}
.beer-container a {
  display: inline-block;
  text-decoration: none;
  color: #777;
  border: 1px solid #b2b2b2;
  padding: 10px 30px;
  border-radius: 4px;
  font-size: 14px;
  transition: all 0.2s;
}

.beer-container a:hover {
  transform: scale(1.1);
}

@media (min-width: 768px) {
  .beers-section {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .beer-container {
    margin: 20px 5px;
    width: 350px;
    padding: 20px 0 80px
  }

}
